/* Sidebar Container */
.sidebar {
  background: var(--glass);
  backdrop-filter: blur(16px);
  border-radius: 20px;
  padding: 2rem;
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow-md);
  transition: all 0.3s ease;
  width: 100%; /* Ensure full width on mobile */
}

/* Profile Card */
.profile-card {
  text-align: center;
  margin-bottom: 2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content vertically */
}

/* Avatar */
.avatar-container {
  margin-bottom: 1rem;
  display: flex;
  justify-content: center; /* Center avatar horizontally */
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--primary);
  transition: all 0.3s ease;
}

.avatar-face {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Profile Info */
.profile-info {
  text-align: center; /* Center profile info */
}

.profile-info h1 {
  font-size: 1.4rem;
  margin-bottom: 0.3rem;
}

.title {
  font-size: 0.9rem;
  color: var(--light-3);
  margin-bottom: 1rem;
}

.location-chip {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  color: var(--light-2);
}

/* Contact Info */
.contact-info,
.quick-stats {
  margin-top: 1.5rem;
}

.contact-item,
.stat-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin-bottom: 0.8rem;
}

.contact-icon,
.nav-icon {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--glass);
  border-radius: 50%;
  color: var(--primary);
  transition: all 0.3s ease;
}

.contact-icon:hover,
.nav-icon:hover {
  background: var(--primary);
  color: var(--dark-1);
}

/* Navigation */
.sidebar-nav {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.8rem 1rem;
  border-radius: 16px;
  color: var(--light-1);
  background: var(--glass);
  border: 1px solid var(--glass-border);
  transition: all 0.3s ease;
}

.nav-item.active,
.nav-item:hover {
  background: var(--primary);
  color: var(--dark-1);
}

.pdf-export {
  margin-top: 1rem;
  background: var(--secondary);
  color: var(--dark-1);
}

.theme-toggle {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--glass-border);
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.3s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background: white;
  transition: 0.3s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: var(--primary);
}

input:focus + .slider {
  box-shadow: 0 0 1px 2px rgba(77, 142, 255, 0.5);
}

input:checked + .slider:before {
  transform: translateX(18px);
}

.mode-label {
  font-size: 0.85rem;
  color: var(--light-3);
  vertical-align: middle;
  margin-left: 0.75rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .sidebar {
    width: 100%; /* Full width on mobile */
    padding: 1rem; /* Reduced padding for mobile */
  }

  .profile-card {
    margin-bottom: 1.5rem; /* Adjust spacing for mobile */
  }

  .avatar {
    width: 80px; /* Smaller avatar size for mobile */
    height: 80px;
  }

  .nav-item {
    padding: 0.6rem 0.8rem; /* Reduced padding for mobile */
  }

  .contact-item,
  .stat-item {
    font-size: 0.9rem; /* Smaller font size for mobile */
  }
}