.homepage {
  position: relative;
  z-index: 0;
}

.section-container {
  max-width: 100%;
  margin: 0 auto;
  animation: fadeIn 0.8s ease-out forwards;
}

.section-title {
  color: var(--light-1);
  margin: 1.5rem 0 2rem;
  font-size: 2.2rem;
  font-weight: 800;
  position: relative;
  display: inline-block;
  letter-spacing: -0.5px;
}

.section-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, var(--primary), var(--secondary));
  border-radius: 4px;
}

.section-content {
  background: var(--glass);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--glass-border);
  margin-bottom: 2.5rem;
  transition: all var(--transition-normal);
}

.competencies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.competencies li {
  background: rgba(77, 142, 255, 0.2);
  padding: 0.3rem 0.9rem;
  border-radius: 20px;
  font-size: 0.85rem;
  border: 1px solid rgba(77, 142, 255, 0.3);
  transition: all 0.2s ease;
}

.experience-timeline {
  position: relative;
  padding-left: 1.5rem;
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
  height: calc(100% - 20px);
  width: 3px;
  background: linear-gradient(to bottom, var(--primary), var(--secondary));
  border-radius: 3px;
}

.experience-item {
  position: relative;
  margin-bottom: 2.5rem;
  padding-left: 1.8rem;
  transition: all var(--transition-normal);
  background: var(--glass);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 1.5rem;
  border: 1px solid var(--glass-border);
}

.experience-item:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-sm);
}

.experience-item::before {
  content: '';
  position: absolute;
  left: -0.5rem;
  top: 1.8rem;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--primary);
  border: 3px solid var(--dark-2);
  box-shadow: 0 0 0 4px rgba(77, 142, 255, 0.3);
}

.education-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.education-item {
  background: var(--glass);
  padding: 1.5rem;
  border-radius: 16px;
  border-left: 4px solid var(--primary);
  border: 1px solid var(--glass-border);
  transition: all var(--transition-normal);
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
}

.certifications {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--glass-border);
}

.certifications li {
  margin-bottom: 0.8rem;
  padding-left: 1.5rem;
  position: relative;
}

