body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: var(--dark-1);
  color: var(--light-1);
  overflow-x: hidden;
  transition: background-color 0.5s ease;
}

* {
  box-sizing: border-box;
}

button, input, select, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}

/* Glow Cursor Trail */
.homepage::before {
  content: '';
  position: fixed;
  top: var(--mouse-y);
  left: var(--mouse-x);
  width: 30px;
  height: 30px;
  transform: translate(-50%, -50%);
  background: radial-gradient(circle, var(--primary), transparent 60%);
  pointer-events: none;
  z-index: 9999;
  opacity: 0.3;
  border-radius: 50%;
  transition: all 0.1s ease-out;
  animation: glow 1s infinite alternate;
}

@keyframes glow {
  from { opacity: 0.2; filter: blur(10px); }
  to { opacity: 0.5; filter: blur(20px); }
}