/* ===== GLOBAL STYLES ===== */
:root {
  --dark-1: #0a0a0a;
  --dark-2: #1a1a1a;
  --light-1: #f5f5f5;
  --light-2: #eaeaea;
  --light-3: #cccccc;
  --primary: #4d8eff;
  --secondary: #6d7acc;
  --glass: rgba(255, 255, 255, 0.1);
  --glass-border: rgba(255, 255, 255, 0.2);
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 15px 25px rgba(0, 0, 0, 0.15);
  --transition-normal: all 0.3s ease;
}

.light-theme {
  --dark-1: #ffffff; /* Light theme background */
  --dark-2: #f5f5f5; /* Secondary light background */
  --light-1: #0a0a0a; /* Text color for light theme */
  --light-2: #333;   /* Secondary text color */
  --light-3: #666;   /* Tertiary text color */
  --glass: rgba(0, 0, 0, 0.1); /* Glassmorphism effect for light theme */
  --glass-border: rgba(0, 0, 0, 0.2); /* Border for glassmorphism */
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.2);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 15px 25px rgba(0, 0, 0, 0.4);
}

.dark-theme {
  --dark-1: #1a1a1a;
  --dark-2: #2d2d2d;
  --light-1: #ffffff;
  --light-2: #f0f0f0;
  --light-3: #d9d9d9;
  --glass: rgba(40, 40, 40, 0.15);
  --glass-border: rgba(255, 255, 255, 0.1);
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.2);
  --shadow-md: 0 10px 15px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 15px 25px rgba(0, 0, 0, 0.4);
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: var(--dark-1);
  color: var(--light-1);
  overflow-x: hidden;
  transition: background-color 0.5s ease;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

/* Mouse Glow Effect */
.homepage::before {
  content: '';
  position: fixed;
  top: var(--mouse-y);
  left: var(--mouse-x);
  width: 30px;
  height: 30px;
  transform: translate(-50%, -50%);
  background: radial-gradient(circle, var(--primary), transparent 60%);
  pointer-events: none;
  z-index: 9999;
  opacity: 0.3;
  border-radius: 50%;
  transition: all 0.1s ease-out;
  animation: glow 1s infinite alternate;
}

@keyframes glow {
  from { opacity: 0.2; filter: blur(10px); }
  to { opacity: 0.5; filter: blur(20px); }
}
.layout-grid {
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 2rem;
  padding: 2rem;
}

.main-content {
  transition: all 0.3s ease;
}

@media (max-width: 1024px) {
  .layout-grid {
    grid-template-columns: 1fr;
  }

  .sidebar-container {
    position: static;
    transform: none !important;
    width: 100%;
    margin-bottom: 2rem;
  }
}